.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    border: rgb(245, 34, 6) 1px solid;
    padding: 10px;
}

.dialogsItems .active {
    color: yellow;
}

.messages {
    border: tomato 1px solid;
    padding: 10px;
}

.messages .message {
    color: teal;
}